import {
  SfHeading,
  SfPrice,
  SfRating,
  SfSelect,
  SfAddToCart,
  SfGallery,
  SfIcon,
  SfBadge,
  SfColor
} from '@storefront-ui/vue';

import { ref, computed } from '@vue/composition-api';
import {
  useCart,
  productGetters,
  useReview,
  reviewGetters
} from '@vue-storefront/shopify';
import MobileStoreBanner from '~/components/MobileStoreBanner.vue';
import LazyHydrate from 'vue-lazy-hydration';

export default {
  name: 'Product',
  transition: 'fade',
  props: {
    productModel: {
      default: [] || {},
      type: Array || Object
    }
  },
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  setup(props, context) {
    const qty = ref(1);
    const { addItem, loading } = useCart();
    const { reviews: productReviews } = useReview(
      'productReviews'
    );
    const product = computed(
      () =>
        productGetters.getFiltered(props.productModel, {
          master: true,
          attributes: context.root.$route.query
        })[0]
    );
    const productDescription = computed(() =>
      productGetters.getDescription(product.value)
    );
    const productDescriptionHtml = computed(() =>
      productGetters.getDescription(product.value, true)
    );
    const options = computed(() => productGetters.getAttributes(product.value));
    const configuration = computed(() =>
      productGetters.getAttributes(product.value, ['color', 'size'])
    );
    const reviews = computed(() =>
      reviewGetters.getItems(productReviews.value)
    );

    // TODO: Breadcrumbs are temporary disabled because productGetters return undefined. We have a mocks in data
    const breadcrumbs = computed(() =>
      productGetters.getBreadcrumbs
        ? productGetters.getBreadcrumbs(product.value)
        : props.fallbackBreadcrumbs
    );
    const productGallery = computed(() =>
      productGetters.getGallery(product.value).map((img) => ({
        mobile: { url: img.small },
        desktop: { url: img.normal },
        big: { url: img.big }
      }))
    );

    const updateFilter = (filter) => {
      console.log(filter);

      /* context.root.$router.push({
        path: context.root.$route.path,
        query: {
          ...configuration.value,
          ...filter
        }
      }); */
    };

    return {
      updateFilter,
      configuration,
      product,
      productDescription,
      productDescriptionHtml,
      reviews,
      reviewGetters,
      averageRating: computed(() =>
        productGetters.getAverageRating(product.value)
      ),
      totalReviews: computed(() =>
        productGetters.getTotalReviews(product.value)
      ),
      options,
      breadcrumbs,
      qty,
      addItem,
      loading,
      productGetters,
      productGallery
    };
  },
  components: {
    SfColor,
    SfHeading,
    SfPrice,
    SfRating,
    SfSelect,
    SfAddToCart,
    SfGallery,
    SfIcon,
    SfBadge,
    MobileStoreBanner,
    LazyHydrate
  },
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  data() {
    return {
      stock: 5
    };
  }
};
