import {
  SfHero,
  SfBanner,
  SfCallToAction,
  SfSection,
  SfCarousel,
  SfProductCard,
  SfImage,
  SfBannerGrid,
  SfHeading,
  SfArrow,
  SfButton
} from '@storefront-ui/vue';
import featuredProduct from '../components/FeaturedProduct';
import blogPosts from '../components/BlogPosts';
import RelatedProducts from '~/components/RelatedProducts.vue';
import InstagramFeed from '~/components/InstagramFeed.vue';
import { useProduct, useCart, productGetters } from '@vue-storefront/shopify';
import { computed } from '@vue/composition-api';
import { onSSR } from '@vue-storefront/core';
import MobileStoreBanner from '~/components/MobileStoreBanner.vue';
import LazyHydrate from 'vue-lazy-hydration';

export default {
  name: 'Home',
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  setup() {
    const { products: relatedProducts, search: productsSearch, loading: productsLoading } = useProduct('relatedProducts');
    const { cart, load: loadCart, addItem: addToCart, isOnCart } = useCart();

    onSSR(async () => {
      await productsSearch({ catId: 123, limit: 8 });
      await loadCart();
    });
    return {
      products: computed(() => productGetters.getFiltered(relatedProducts.value, { master: true })),
      getChkId: computed(()=> cart.value.id),
      productsLoading,
      productGetters,
      addToCart,
      isOnCart
    };
  },
  components: {
    InstagramFeed,
    SfHero,
    RelatedProducts,
    SfBanner,
    SfCallToAction,
    SfSection,
    SfCarousel,
    blogPosts,
    SfProductCard,
    SfImage,
    SfBannerGrid,
    SfHeading,
    SfArrow,
    SfButton,
    MobileStoreBanner,
    LazyHydrate,
    featuredProduct
  },
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  data() {
    return {
      heroes: [
        {
          title: 'Colorful summer dresses are already in store',
          subtitle: 'SUMMER COLLECTION 2019',
          buttonText: 'Learn more',
          background: '#eceff1',
          image: '/homepage/bannerH.webp',
          link: '/c/women/women-clothing-shirts'
        },
        {
          title: 'Colorful summer dresses are already in store',
          subtitle: 'SUMMER COLLECTION 2019',
          buttonText: 'Learn more',
          background: '#efebe9',
          image: '/homepage/bannerA.webp',
          link: '/c/women/women-shoes-sandals',
          className:
            'sf-hero-item--position-bg-top-left sf-hero-item--align-right'
        },
        {
          title: 'Colorful summer dresses are already in store',
          subtitle: 'SUMMER COLLECTION 2019',
          buttonText: 'Learn more',
          background: '#fce4ec',
          image: '/homepage/bannerB.webp',
          link: '/c/women/women-clothing-dresses'
        }
      ],
      banners: [
        {
          slot: 'banner-A',
          subtitle: 'Dresses',
          title: 'Cocktail & Party',
          description:
            'Find stunning women\'s cocktail dresses and party dresses. Stand out in lace and metallic cocktail dresses from all your favorite brands.',
          buttonText: 'Shop now',
          image: {
            mobile: '/homepage/bannerB.webp',
            desktop: '/homepage/bannerF.webp'
          },
          class: 'sf-banner--slim desktop-only',
